.main-menu {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  margin-bottom: 3em;
}

.brand {
  padding: 1em 2em;
  cursor: default;
}

.menu-link {
  padding: 1em 2em;
}

.menu-link a {
  text-decoration: none;
  color: #000;
  font-weight: 600;
}

.shop-cart {
  padding: 0.5em 1em;
  border-radius: 50%;
  border: 2px solid #222;
  cursor: pointer;
}

.shop-cart {
  float: right;
}

@media (min-width: 1281px) {
  
  .menu-main {
    width: 100%;
  }
  .menu ul{
    display: table;
    margin: 0 auto;
    padding-left: 5em;
  }
  .menu-link {
    display: block;
    float: left;
    padding: 1em 0.4em 0.2em 0.4em;
    font-size: 1.5em;
  }
  .brand{
    width: 35vw;
    padding: 0.5em 0.1em 0.2em 1.2em;
    font-size: 2em;
  }
  .shop-cart {
    font-size: 1em;
    padding: 0.5em 1em;
    border-radius: 50%;
    border: 2px solid #222;
    margin: 1em 0.1rem;
    cursor: pointer;
  }
  .brand,
  .shop-cart {
    display: -webkit-inline-box;
    justify-content: space-between;
  }
  
}

