.main-footer {
  display: block;
  margin-top: 3em;
  background-color: rgb(101, 110, 103);
  justify-content: space-between;
  font-size: 1.1em;
  padding: 0 0.7em;
}

.owner {
  color: rgb(202, 187, 125);
  font-weight: 600;
}

.sub-owner {
  text-decoration: none;
  color: rgb(187, 216, 108);
  font-weight: 600;
}

@media (min-width: 1281px) {
  .main-footer {
    display: flex;
    font-size: 1em;
    justify-content: space-around;
    padding: 0 0.2em;
  }
  .our-store span {
    font-size: 1em;
  }
  h4 {
    font-size: 1em;
  }
  p {
    font-size: 1em;
  }
  .blog-posts {
    margin-bottom: 1em;
  }
  .blog-posts p {
    font-size: 1em;
  }
  .blog-posts span {
    font-size: 1em;
  }
  .support span {
    font-size: 1em;
    font-weight: 600;
    display: block;
  }
  .our-store span {
    display: block;
  }
}

